body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #900101; }

.ui image header {
  padding-top: 80px; }

.navbar {
  background-color: black;
  height: 200px;
  text-align: center; }
  .navbar .center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-top: 50px;
    width: 250px; }

.menu {
  background-color: black;
  text-align: center;
  padding: 10px; }

.searchbar {
  text-align: center;
  padding: 10px; }
  .searchbar .searchbar_input {
    width: 300px;
    border-radius: 0; }
    .searchbar .searchbar_input input {
      border-radius: 0;
      border-color: black; }
  .searchbar .button {
    background: black;
    color: red;
    border-radius: 0;
    border: 1px solid rgba(34, 36, 38, 0.15); }

ul {
  background-color: white; }

.radiodiv {
  text-align: center;
  color: white;
  padding-bottom: 10px; }
  .radiodiv .radioinput {
    padding: 5px; }

.w3-ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }
  .w3-ul li {
    padding: 8px 16px;
    border-bottom: 1px solid #ddd; }
  .w3-ul li:last-child {
    border-bottom: none; }

.w3-card-4, .w3-hover-shadow:hover {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.19);
  width: auto; }

.w3-bar {
  width: 100%;
  overflow: hidden; }
  .w3-bar a {
    padding: 0; }

/* W3.CSS 4.12 November 2018 by Jan Egil and Borge Refsnes */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

.character_name {
  font-size: 20px;
  padding-left: 15px; }

.ui.avatar.image, .ui.avatar.image img, .ui.avatar.image svg, .ui.avatar.images .image, .ui.avatar.images img, .ui.avatar.images svg {
  width: 5em;
  height: 5em; }

a {
  padding: 10px;
  color: white; }

a:hover {
  background-color: yellow;
  color: red; }

a.active {
  background-color: yellow;
  color: red; }

.ui.segment:last-child {
  background-color: rgba(255, 255, 255, 0.4);
  margin: 50px; }

.noCharacter {
  width: 100% !important;
  margin: 0 auto;
  padding: 50px;
  text-align: center; }

.modified {
  text-align: right; }

.bar-contnet {
  padding-top: 10px; }

.listrow {
  color: black; }

.listrow:hover {
  color: red;
  background-color: white; }

.filter {
  background-color: White;
  text-align: center;
  padding: 20px; }

.filter a {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px;
  color: black; }

.filter a:hover {
  background-color: red;
  color: white; }

.filter a.active {
  background-color: white;
  color: red; }

.selectevent {
  text-align: center;
  color: red; }

.ui card noCharacter:before {
  content: "";
  display: block;
  padding-top: 100%;
  /* initial ratio of 1:1*/ }

.column a:hover {
  background: transparent; }


.char-card {
  text-align: center; }
  .char-card a:hover {
    background-color: transparent; }

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -20px;
  padding: 15px;
  color: #527988;
  font-weight: bold;
  font-size: 18px;
  -webkit-transition: 0.6s ease;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.next {
  right: 0;
  border-radius: 3px 0 0 3px; }

.prev {
  left: 0;
  border-radius: 3px 0 0 3px; }

.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.ui.label.label {
  padding: 5px;
  margin: 3px;
  background-color: #900101;
  color: white; }

Label {
  padding: 5px; }

